___CSS_LOADER_EXPORT___.push([module.id, `.positioning-container {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 99;

	height: 100vh;
	width: 100vw;
	background-color: transparent;
	display: flex;
}

.popup-container {
	display: flex;
	flex-direction: column;
	margin: auto;

	height: calc(100vh - .5em);
	width: calc(100vw - .5em);
	background-color: #aac27ee8;
	font-family: RobotoFlex;
	border: .25em solid red;
	box-shadow: 0px 0px 25px red;
	border-radius: .5em;
}

.close-button {
	align-self: flex-end;
	height: 5rem;
}

.popup-header-container {
	display: flex;
	flex-direction: column;
}

.popup-header {
	text-align: center;
	margin-top: 5rem;
	font-size: 4em;
}
.popup-header-p {
	font-size: 2em;
	margin: auto;
}

.popup-body {
	margin-top: 1em;
	text-align: center;
	font-size: 2.75em;
}

.github-link-container {
	margin-top: 5em;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	height: 5em;
	width: 100%;
	border-top: .25em solid red;
	border-bottom: .25em solid red;
}

.github-text {
	font-size: 3rem;
	margin: auto 0;
}

.github-icon {
	width: 5em;
}

/*the @media() query doesn't recognize the value of var(--mobile-screen)*/
@media (width > 450px) {
	.popup-container {
		max-width: var(--mobile-screen);
		height: calc(100vh - 4em);
		margin: 2em auto;
	}
}
